@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap");

@layer base {
  html {
    font-size: 62.5%;
    box-sizing: border-box;
  }

  *,
  *::before,
  *::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
  }

  body {
    font-family: "Nunito Sans", sans-serif;
  }

  #root {
    min-height: 100vh;
  }

  @media only screen and (max-width: 500px) {
    html {
      font-size: 55%;
    }
  }
}
